@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

:root {
  /* --button-width: 12ch; */
  --button-height: 5ch;
  --button-color: rgba(48, 43, 49, 0.792);
  --button-color-hover: rgba(64, 53, 64, 0.783);
  --button-color-clicked-hover: rgba(255, 0, 0, 0.135);
  --button-border: solid rgba(54, 50, 75, 0.669) 2px;
  --button-border-hover: solid rgba(162, 34, 143, 0.384) 2px;
  --button-border-radius: 6px;
  --button-font-size: 20px;
  --big-button-color: rgba(107, 110, 122, 0.342);
  --big-button-color-hover: rgba(39, 23, 42, 0.402);
  --big-button-border: #9090c387 solid 2px;
  --big-button-border-radius: 10px;
  --big-button-border-effect: rgba(125, 105, 40, 0.422);
  --big-button-border-hover: solid rgba(162, 34, 143, 0.194) 2px;
  --simple-button-color: rgba(103, 81, 107, 0.94);
  --simple-button-color-hover: rgba(153, 122, 165, 0.514);
  --simple-button-border: #ff7f5018 solid 1px;
  --simple-button-border-radius: 15px;
  --simple-button-border-effect: rgba(125, 105, 40, 0.422);
  --simple-button-border-hover: solid rgba(162, 34, 143, 0.194) 2px;
  /* Used to calculate how the layout should respond when sidebar opens */
  --resize: 400px;
  --linearTheme: linear-gradient(rgba(26, 44, 79, 0.395), rgb(5, 36, 53));
  --tool-background-color: rgba(23, 23, 36, 0.943);
}

/* Media query for computers */
@media screen and (min-width: 768px) {

}

/* Generally smaller screens */
@media screen and (max-width: 1000px) {
  /* body {
    overflow: scroll;
  } */

  #panesElements {
    left: 0 !important;
    width: auto !important;
  }

  #topbar {
    left: 0 !important;
  }

  .focusSideBarOn {
    left: 75vw;
    width: 25vw;
    height: 100vh;
  }
}
/* Mobile screen (PORTRAIT MODE) */
@media screen and (max-width: 800px) {
  #topHeader h1 {
    display: none;
  }
}
/* Mobile screen (NOT PORTRAIT MODE) */
@media screen and (max-width: 500px) {
  #topHeader h1 {
    display: none;
  }
  #toolbar button {
    min-width: 100px !important;
    width: 200px;
    font-size: 15px;
    height: 1vh;
    padding: 1px;
    margin: 0;
  }
  #toolbar button:nth-child(1) {
    margin-left: 18ch;
  }
  /* Hide all the other elements */
  .focusSideBarOn > * {
    display: none;
  }
  /* Show the minimizer button and the time */
  #startFocus {
    display: inline;
  }
  /* Better font size for mobile */ 
  .paneStyle .description {
    font-size: 2.5ch;
  }
}

.themedButton {
  color: white;
  font-size: var(--button-font-size);
  background-color: var(--button-color);
  border: var(--button-border);
  transition: 0.5s;
  border-radius: var(--button-border-radius);
  cursor: pointer;
  padding: 0.5ch;
  text-align: center;
  min-height: 45px;
}

.themedButton:hover {
  background-color: var(--button-color-hover);
  border: var(--button-border-hover);
}

.themedButtonClicked {
  color: white;
  font-size: var(--button-font-size);
  transition: 0.5s;
  border-radius: var(--button-border-radius);
  cursor: pointer;
  padding: 0.5ch;
  text-align: center;
  background-color: var(--button-color-clicked-hover);
  border: var(--button-border-hover);
}

.bigThemedButton {
  color: white;
  background-color: var(--big-button-color);
  border: var(--big-button-border);
  font-size: 2ch;
  /* padding: 1vh; */
  min-width: 12ch;
  min-height: 50px;
  height: 4ch;
  /* width: 16ch; */
  transition: 0.4s;
  border-radius: var(--big-button-border-radius);
  cursor: pointer;
  box-shadow: 2px 2px 1px 0px rgba(18, 57, 88, 0.251);
}

.bigThemedButton:hover {
  animation-name: conicBorderEffect;
  animation-duration: 1s;
  background-color: var(--big-button-color-hover);
  border: var(--big-button-border);
  /* x, y, blur, spread */
  box-shadow: 0px 2px 0px 0px rgb(46, 44, 46);
}

@keyframes conicBorderEffect {
  0% {border-top-color: var(--big-button-border-effect)}
  25% {border-right-color: var(--big-button-border-effect); border-top-color: none;}
  50% {border-bottom-color: var(--big-button-border-effect); border-right-color: none;}
  75% {border-left-color: var(--big-button-border-effect); border-bottom-color: none;}
  100% {border: var(--big-button-border)}
}

.simpleThemedButton {
  color: white;
  background-color: var(--simple-button-color);
  border: var(--simple-button-border);
  font-size: 1.6ch;
  width: 5ch;
  height: 5ch;
  transition: 0.4s;
  border-radius: var(--simple-button-border-radius);
  cursor: pointer;
  box-shadow: 2px 2px 1px 0px rgba(18, 57, 88, 0.251);
  font-weight: bolder;
}
.simpleThemedButton:hover {
  background-color: var(--simple-button-color-hover);
  border: var(--simple-button-border);
  box-shadow: 0px 2px 0px 0px rgb(46, 44, 46);
}

.fadeInEffect {
  animation-name: fadeInEffect;
  animation-duration: 1s;
}

@keyframes fadeInEffect {
  from {opacity: 0;}
  to {opacity: 1;}
}
/* Delay animation by 0.05s so the other popOutEffect will take 0.05s and popInEffect follows after */
.popInEffect {
  animation-name: popInEffect;
  animation-duration: 0.1s;
  animation-timing-function: ease-in-out;
  /* animation-timing-function: cubic-bezier(0.49, 0.54, 0.01, 0.97); */
}
@keyframes popInEffect {
  from {padding: 0.1vw; font-size: 0.01ch; opacity: 0; z-index: 4;}
  to {padding: 0.5ch; font-size: var(--button-font-size); opacity: 1;}
}
.popOutEffect {
  z-index: -1;
  /* Would have used display but animation doesn't work */
  visibility: hidden;
  position: absolute;
}

body {
  background-image: url('../assets/blur.png');
  background-color: #282c34;
  color: white;
  overflow-x: hidden;
}

#splashscreen {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.454);
  visibility: hidden;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  z-index: -1;
  /* backdrop-filter: blur(30px); */
}

img {
  width: -webkit-fill-available;
  height: -webkit-fill-available;
  cursor: pointer;
  background-color: white;
  border-radius: 20px;
}

#topbar {
  margin-left: 20px;
  position: relative;
  left: calc(var(--resize));
}

#topHeader {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
}

#toolbar {
  display: flex;
  align-items: center;
  margin-left: 2%;
  overflow-x: hidden;
}

#toolbar button {
  min-width: 150px;
  max-width: 25ch;
  /* top | left and right | bottom */
  margin-left: 10px;
  margin-top: 10px;
}

#topHeader>* {
  margin: 0;
}

#topHeader h1 {
  margin-right: 2x;
  padding: 0;
  font-size: 200%;
  /* Fixes the problem with the toolbar moving left and right arrows */
  width: fit-content;
}

#focusSession {
  transition: 1s;
  text-align: center;
}

#focusSession h1 {
  transition: 0s;
}

/* focusSideBarOn is in a media query */
.focusSideBarOn {
  left: 96vw;
  width: 15vw;
  height: 100vh;
  backdrop-filter: none !important;
}
.focusSideBarOn:hover {
  left: 85vw;
  width: 15vw;
  height: 100vh;
}

.focusSideBarOff {
  /* left: 0; */
}

#main {
  margin-left: 4vw;
}

.sidebarOff {
  left: 0 !important;
}

/* For the toggle sidebar */
.sidebarOffToggle {
  content: '>' !important;
}

#sidebar {
  padding: 0;
  font-size: 2.2ch;
  height: 100%;
  background: var(--linearTheme);
  background-color: black;
  left: 0;
  width: var(--resize);
  border-radius: 10px;
  border-top-left-radius: 0;
  position: fixed;
  margin-top: 0 !important;
  z-index: 1;
  transition: 0.5s;
  animation-name: firstOpenSidebar;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
  overflow-x: auto;
}

@keyframes firstOpenSidebar {
  from {
    width: 0;
  }

  to {
    width: var(--resize);
  }
}

#sidebar .section {
  width: 95%;
  height: 10%;
}

#sidebar .section * {
  padding: 0;
  margin: 0;
}

#sidebar .section p {
  display: flex;
  margin: 0;
  padding: 0;
  background-color: rgba(0, 0, 0, 0.009);
  word-wrap: break-word;
  width: 90%;
  font-size: 80%;
}

#sidebar .section button {
  font-size: 125%;
  margin: 0;
  padding: 0;
  position: relative;
}
.section button:hover {
  margin: 0.2vw;
}
#sidebar .section button:hover {
  margin: 1%;
}

/* For main page and section page */
.section button:hover {
  background-color: rgb(40, 40, 40);
}

#toggleSidebar {
  font-size: 30px;
  opacity: 0.8;
