@keyframes animate {
  0% {
    width: 0;
    visibility: visible;
  }

  40% {
    width: 100%;
  }

  60% {
    width: 100%;
  }

  100% {
    width: 0;
  }
}

.splashAnimate {
  animation-name: animate;
  animation-duration: 1s;
  animation-timing-function: ease-in;
}

#panesElements {
